.icon {
  &--arrow {
    width: 1rem;
    transition: all $mid;
    path {
      fill: none;
      stroke: #ffdc77;
      stroke-width: 1px;
    }
  }
  &--document {
    display: inline-block;
    width: 59px;
    height: 74px;
    background-image: url(../assets/images/icon-document.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}
