.btn {
  display: inline-block;
  margin: 0;
  padding: 0;
  background-color: transparent;
  text-align: center;
  border-width: $btn-border;
  border-style: solid;
  border-color: transparent;
  cursor: pointer;

  &--outline {
    width: 100%;
    max-width: 290px;
    height: 65px;
    color: #fff;
    font-weight: 500;
    line-height: 60px;
    border-color: #ffdc77;
    box-shadow: -4px 6px 32px rgba(0, 0, 2, 0.25);
    .icon {
      position: relative;
      top: 2px;
      margin-left: 10px;
    }
    &:hover .icon {
      transform: translateX(50%);
    }
  }

  &--to-top {
    display: none;
    position: fixed;
    top: 40%;
    left: 80px;
    width: 80px;
    height: 80px;
    box-shadow: 0 0 25px rgba(7, 0, 104, 0.15);
    background-color: #fff;
    border-radius: 40px;
    transition: background-color $mid;
    &:after {
      position: absolute;
      content: "";
      top: 33px;
      left: 32px;
      width: 14px;
      height: 14px;
      border-top: 1px solid #1f1f1f;
      border-left: 1px solid #1f1f1f;
      transform: rotate(45deg);
    }
    &:hover {
      background-color: rgba(#fff, 0.9);
    }
    @media (max-width: 1599px) {
      display: none !important;
    }
  }
}
