.contacts {
  padding: 36px 19px 35px;
  text-align: center;
  background-image: url(../assets/images/contact-bg.jpg);
  @media (min-width: 640px) {
    padding: 36px 58px 35px;
  }
  &__header {
    margin: 0;
    color: #fff;
    font-size: 30px;
    font-weight: 300;
    letter-spacing: 0.05em;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0 32px;
    color: #fff;
    @media (min-width: 1200px) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    @media (min-width: 640px) {
      width: 28vh;
    }
    @media (min-width: 1200px) {
      width: auto;
      margin: 0;
    }
  }
  &__img {
    min-width: 65px;
    height: 65px;
    background-repeat: no-repeat;
    background-position: center;
    &--email {
      background-image: url(../assets/images/icon-mail.png);
    }
    &--phone {
      background-image: url(../assets/images/icon-phone.png);
    }
    &--address {
      background-image: url(../assets/images/icon-address.png);
    }
  }
  &__desc {
    margin-left: 20px;
    line-height: 1.2;
    text-align: left;
    span {
      display: block;
    }
    a {
      color: #fff;
      font-size: 16px;
    }
  }

  &__subheader {
    margin: 0 0 10px;
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    line-height: 1.24;
    letter-spacing: 0.05em;
    strong {
      color: #ffee91;
      font-weight: 300;
    }
  }
}
