.consultation {
  padding: 65px 19px 35px;
  text-align: center;
  background-image: url(../assets/images/consultation-bg.jpg);
  @media (min-width: 640px) {
    padding: 65px 58px 35px;
  }
  &__header {
    margin: 0 0 10px;
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    line-height: 1.24;
    letter-spacing: 0.05em;
    strong {
      color: #ffee91;
      font-weight: 300;
    }
  }
  &__subheader {
    margin: 0 0 10px;
    color: #fff;
    font-size: 16px;
    font-weight: 300;
  }
  &__btn {
    margin-top: 19px;
    margin-bottom: 12px;
  }
  &__info {
    color: #fff;
    font-size: 13px;
  }
}
