.form {
  width: 100%;
  &__inline {
    @media (min-width: 1050px) {
      display: flex;
      justify-content: space-between;
    }
    > div:not(:last-child) {
      margin-right: 23px;
    }
  }
  &__group {
    width: 100%;
    margin-bottom: 15px;
    @media (min-width: 1050px) {
      max-width: 240px;
      margin-bottom: 0;
    }
  }
  &__label {
    color: #fff;
    font-size: 10px;
    text-transform: uppercase;
    white-space: nowrap;
  }
  &__input {
    display: block;
    width: 100%;
    height: 60px;
    padding: 0 15px;
    background-color: #fff;
    text-align: center;
    border: 3px solid #eee;
    transition: all $slow;
    &::placeholder {
      color: #7f7f7f;
      font-size: 18px;
      transition: all $fast;
    }
    &:focus {
      border: 3px solid #ffdc77;
      &::placeholder {
        opacity: 0;
      }
    }
  }
}
