.footer {
  &__nav {
    color: #fff;
    background-color: #000;
  }
  &__bottom {
    color: #000;
    background-color: #fff;
  }
  &__info {
    display: flex;
    flex-direction: column;
    padding: 30px 40px;
    @media (min-width: 1024px) {
      flex-direction: row;
      justify-content: space-around;
    }
    a {
      color: #000;
    }
  }
}


.footer-nav {
  padding: 40px;
  &__copyright {
    margin-bottom: 30px;
    font-weight: 500;
  }
  &__menu {
    @media (min-width: 1200px) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__list {
    margin-bottom: 20px;
    @media (min-width: 1200px) {
      margin: 0;
    }
  }
  &__link {
    color: #fff;
    font-weight: 500;
    line-height: 30px;
    border-bottom: 1px solid #808080;
    &:focus, &:hover, &:active {
      border-bottom: 1px solid #000;
    }
  }
}
