.services {
  padding: 19px;
  @media (min-width: 640px) {
    padding: 35px;
  }
  &__tile {
    display: block;
    @media (min-width: 1050px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  &__title {
    margin: 10px 0;
    color: #070068;
    font-size: 20px;
    text-align: center;
  }
  &__clients {
    text-align: center;
    > img {
      display: inline-block;
      width: 100%;
      height: auto;
    }
  }
}


.services-tile {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 18px;
  color: #fff;
  background-repeat: no-repeat;
  border-radius: 4px;
  border: 1px solid #eaeaea;
  overflow: hidden;
  @media (min-width: 1050px) {
    width: calc(50% - 15px);
    max-width: 392px;
  }
  &:hover {
    color: #ffee91;
  }
  &:nth-child(odd) {
    margin-right: 18px;
  }
  > img {
    display: block;
    width: 100%;
    height: auto;
  }
  > div {
    position: absolute;
    top: 50%;
    right: 0;
    width: 65%;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    transform: translateY(-50%);
    @media (min-width: 640px) {
      font-size: 27px;
      line-height: 30px;
    }
    @media (min-width: 1050px) {
      width: 67%;
      max-width: 255px;
      font-size: 20px;
      line-height: 26px;
    }
    @media (min-width: 1144px) {
      width: 70%;
      font-size: 27px;
      line-height: 30px;
    }
  }
}

