.sub-menu {
  width: 300px;
  background-color: #fff;
  &--fixed {
    position: fixed;
    //bottom: 0;
    //z-index: 1;
  }
  &__wrapper {
    padding: 37px 30px;
  }
  &__header {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.075em;
  }
  &__link {
    display: inline-block;
    padding: 20px 0;
    color: #000;
    font-weight: 700;
    line-height: 1.25;
  }
  &__sublink {
    display: inline-block;
    position: relative;
    padding-left: 16px;
    color: #000;
    font-size: 14px;
    font-weight: 300;
    line-height: 1.4;
    &:before,
    &:after {
      position: absolute;
      content: "";
      left: 0;
      width: 1px;
      height: 8px;
      background-color: #696969;
    }
    &:before {
      top: 3px;
      transform: rotate(-45deg);
    }
    &:after {
      top: 8px;
      transform: rotate(45deg);
    }
  }
}

.sub-menu-pseudo {
  position: absolute;
  width: 100%;
}
