.our-company {
  background-color: #fff;
  &__title {
    margin: 36px 0 0;
    color: #070068;
    font-size: 24px;
    font-weight: 300;
    text-align: center;
    letter-spacing: 0.05em;
  }
  &__subtitle {
    margin: 0;
    color: #000;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.4;
    text-align: center;
  }
  &__slider {
    overflow: hidden;
  }
}


.company-slider {
  margin-top: 20px;
  touch-action: manipulation;
  &__item {
    width: 438px;
    height: 297px;
    margin-right: 7px;
    &:hover {
      .document-slider__img::before {
        opacity: 0.001;
      }
    }
  }
  &__img {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    &::before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: -1px;
      right: -1px;
      content: "";
      background-color: #000;
      opacity: 0;
    }
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
}


.document-slider {
  margin-top: 20px;
  margin-bottom: 13px;
  touch-action: manipulation;
  &__item {
    width: 215px;
    height: 297px;
    margin-right: 7px;
    background-color: #f8f8f8;
    text-align: center;
    transition: all $mid;
    &:hover {
      background-color: #fde69e;
      .document-slider__img::before {
        opacity: 0.75;
      }
    }
  }
  &__title {
    padding-top: 24px;
    padding-bottom: 14px;
    font-size: 11px;
    text-transform: uppercase;
    white-space: nowrap;
  }
  &__img {
    position: relative;
    display: inline-block;
    width: 152px;
    height: 213px;
    box-shadow: -4px 6px 8px rgba(0, 0, 2, 0.2);
    border: 7px solid #1f1e4b;
    &::before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: -1px;
      right: -1px;
      content: "";
      background-color: #07114f;
      background-image: url(../assets/images/icon-magnifier.png);
      background-repeat: no-repeat;
      background-position: center;
      opacity: 0;
    }
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
}
