.sub-header {
  &__nav {
    display: flex;
    justify-content: center;
    > :nth-of-type(1) {
      margin-right: 10px;
      text-align: right;
      @media (min-width: 1024px) {
        margin-right: 47px;
      }
    }
    > :nth-of-type(2) {
      margin-left: 10px;
      @media (min-width: 1024px) {
        margin-left: 47px;
      }
    }
  }
  &__btn {
    padding: 50px 0 60px;
    text-align: center;
  }
}


.sub-header-nav {
  &__link {
    color: #fff;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    @media (min-width: 1024px) {
      font-size: 32px;
      line-height: 50px;
    }
    &:hover {
      color: $accent-color;
    }
  }
}
