.breadcrumbs {
  padding: 0 37px;
  background-color: #fde69e;
  &__list {
    display: flex;
  }
  &__item {
    padding: 14px 0;
    font-size: 14px;
    font-weight: 700;
    white-space: nowrap;
    &:not(:last-child) {
      padding-right: 19px;
      &::after {
        content: ">";
        padding-left: 19px;
        font-weight: 300;
      }
    }
  }
  &__link {
    color: #000;
    font-size: 14px;
    font-weight: 700;
  }
}
