/* Common Styles
----------------------------------------------------------------------------- */
html {
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  background-color: $bg-color;
  background-image: url(../assets/images/bg.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: 70% bottom;
  font-family: $primary-font;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.75;
  opacity: 1;
}

*, *:focus {
  outline: none;
}
*::-moz-focus-inner {
  border: 0 none;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

[hidden] {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition: all $slow;
}

textarea {
  resize: vertical;
}

hr {
  display: block;
  height: 2px;
  border: 0 none;
  border: 0 none;
  margin: 1em 0;
  padding: 0;
  background-color: #f5f5f5;
}

.wrapper {
  position: relative;
  min-width: 320px;
  min-height: 100%;
  overflow-x: hidden;
}

.container {
  position: relative;
  width: 100%;
  max-width: $container-with;
  margin: 0 auto;
  &--wrap {
    display: flex;
    padding: 0 20px;
    @media (min-width: 1200px) {
      padding: 0;
    }
  }
}

.aside {
  position: relative;
  display: none;
  width: 100%;
  min-width: 300px;
  max-width: 300px;
  margin-right: 18px;
  @media (min-width: 780px) {
    display: block;
  }
}

.content {
  width: 100%;
  max-width: 882px;
  background-color: #fff;
  &__block {
    padding: 20px 70px 34px 38px;
    h2,
    h3 {
      margin: 0 0 10px;
      color: #070068;
      font-weight: 300;
    }
    h2 {
      font-size: 32px;
    }
    h3 {
      font-size: 24px;
    }
    h3 + ul {
      margin-top: 30px;
    }
    ul + h3 {
      margin-top: 40px;
    }
    p {
      color: #000;
      font-size: 14px;
      line-height: 24px;
    }
  }
}


.count-list {
  margin-left: -38px;
  margin-right: -70px;
  counter-reset: list;
  > li {
    display: flex;
    align-items: center;
    min-height: 80px;
    padding: 10px 100px 10px 50px;
    font-size: 14px;
    line-height: 24px;
    &:before {
      padding-right: 50px;
      color: #070068;
      font-size: 40px;
      font-weight: 700;
      counter-increment: list;
      content: counter(list);
    }
  }
  > li:nth-child(odd) {
    background-color: #f9f9f9;
  }
}


.document-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  &__link {
    display: block;
    max-width: 200px;
    padding-bottom: 20px;
    color: #000;
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    text-align: center;
    span {
      display: block;
    }
  }
}
